@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&display=swap);
/*************** App - CSS ***************/

:root{
  --primary-white: #fff;
  --primarty-dark: rgba(255, 255, 255, 0);

  --primary-orange: #f9ab00;

  --primary-red: #ef4035;
  --primary-hover-red: #f33501;
}

body {
  font-family: 'Roboto Condensed', sans-serif !important ;
}

.section-heading {
  padding-top: 3rem;
  color: #f9ab00;
  color: var(--primary-orange);
  text-transform: uppercase;
  padding-bottom: 3rem;
}

.section-border {
  border-style: 2px solid #f9ab00;
  border-style: 2px solid var(--primary-orange);
}



.brand:hover {
  cursor: pointer;
	color: #f9ab00 !important;
	-webkit-animation: enable 0.3s linear;
	        animation: enable 0.3s linear;
}

@-webkit-keyframes enable {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    60% {
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}

@keyframes enable {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    60% {
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}


/* Custom CSS */
.padding-left-2 {
  padding-left: 2rem;
}
/*************** Header - CSS ***************/

nav {
	z-index: 2;
}
  
nav a.nav-link {
	font-weight: 100;
	letter-spacing: 0.0375rem;
	cursor: pointer;
}
  
nav ul li {
	text-transform: uppercase;
}
  
.navbar-nav > li > a {
	margin-right: 0.75rem;
	margin-left: 0.75rem;
	color: var(--primary-white) !important;
	border-top: 0.125rem solid var(--primarty-dark) !important;
}
  
a.navbar-brand {
	font-weight: bold;
	font-size: x-large;
	max-width: 17.25rem;
	padding: 0.5rem !important;
	color: var(--primary-white) !important;
}
  
.navbar-nav > li > a.active, .navbar-nav > li > a:focus {
	border-top: 0.125rem solid var(--primary-orange) !important;
}
  
.navbar-nav > li > a:hover {
	border-top: 0.125rem solid var(--primary-orange) !important;
}


/*************** Home - CSS ***************/

.intro-wrapper {
	position: relative;
	background: url(/portfolio/static/media/MainBackground.d6c15d1a.jpg);
	background-size: cover;
	background-position: center;
	height: 100vh;
}

.intro-info {
	display: flex;
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0; 
	flex-direction: column;
	align-items: center;
	justify-content: center;
	text-align: center;
	z-index: 2;
}


/**************** INTRO HEADING **************/
.intro-info h2 {
	color: #f9ab00;
	font-weight: bold;
	padding-bottom: 2rem;
}



/**************** Areas Of Interest **************/
.areas-of-interest {
	font-size: 2rem;
	color: var(--primary-white);
	padding-bottom: 3rem;
}



/**************** Resume **************/
.my-resume {
	font-size: larger;
	text-decoration: none;
	text-transform: uppercase;
	color: var(--primary-white);

	border: 3px solid red;
	border-radius: 0;
	padding: 0.5rem 0.75rem 0.5rem 0.75rem;
	margin-bottom: 3rem;
}

.my-resume:hover {
	cursor: pointer;
	text-decoration: none;
	background-color: red;
	color: var(--primary-white);
	-webkit-animation: enable 0.3s linear;
	        animation: enable 0.3s linear;
}

@-webkit-keyframes enable {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    60% {
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}

@keyframes enable {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    60% {
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}



/**************** LinkedIn / GitHub / Gmail Section **************/
.linkedin-logo, .github-logo, .gmail-logo {
	width: 3.5rem;
	background-color: #fff;
}

.linkedin-logo:hover, .github-logo:hover, .gmail-logo:hover, .my-resume:hover {
	-webkit-animation: enable 0.3s linear;
	        animation: enable 0.3s linear;
}



/**************** MOBILE ****************/
@media(max-width: 768px) {
	.intro-info h1 {
		font-size: 1.8rem !important;
	}
	.areas-of-interest {
		font-size: 1.3rem !important;
	}

	.linkedin-logo, .github-logo, .gmail-logo{
		width: 3rem;
	}
}
/*************** AboutMe - CSS ***************/

.AboutMe {
	background: linear-gradient( rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5) ), url(/portfolio/static/media/SectionBackground.e134a9cc.jpg);
}

.about-me-section {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}



/*** Profile Image ***/
.profile-img {
	width: 18rem;
	border-radius: 50%;
	padding: 0.2rem;
	border: 3px solid var(--primary-orange);
}
  
.profile-img-section {
	text-align: center;
}



.about-me-heading {
	text-transform: uppercase;
	color: var(--primary-orange);
}

.name {
	-webkit-text-decoration: underline solid #f9ab00;
	        text-decoration: underline solid #f9ab00;
}

.about-me-main {
	color: #fff;
	text-align: center;
}

.about-me-sub {
	color: #fff;
}


  
@media(max-width: 768px) {
	.profile-img {
	  width: 10rem;
	}
  
	.about-me-heading {
	  padding-top: 25px;
	}
  
	.about-me-heading, .about-me-sub {
	  padding-left: 1rem;
	  padding-right: 1rem;
	  text-align: center;
	}
}

/*************** Background - CSS ***************/

.Background {
	background: linear-gradient( rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5) ), url(/portfolio/static/media/SectionBackground.e134a9cc.jpg);
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-flow: column;
}

.Background title {
    position: fixed;
}

/*** Vertical timeline line color ***/
.vertical-timeline::before {
    background: #f9ab00 !important;
}


