/*************** Skills - CSS ***************/

.Skills {
	background: linear-gradient( rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5) ), url(/portfolio/static/media/SectionBackground.e134a9cc.jpg);
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-flow: column;
}

.skills-image-container img {
	overflow: hidden;
	background-color: #fff;
	width: 12rem;
	height: 6rem;
}



/* Programming Section */
.programming, .databases, .frameworks, .web-technologies, .other-technologies, .cloud-technologies {
    color: #fff;
}

.programming-skills, .databases-skills, .frameworks-skills, .web-technologies-skills, .other-technologies-skills, .cloud-technologies-skills {
	display: flex;
	flex-direction: row;
}

.gap {
	padding-left: 2rem;
}



@media(max-width: 768px) {
	.skills-image-container img {
		background-color: #fff;
		width: 10rem;
		height: 5rem;
	}

	.programming-skills, .databases-skills, .frameworks-skills, .web-technologies-skills, .other-technologies-skills, .cloud-technologies-skills {
		flex-direction: column;
	}

	.gap {
		padding-top: 1rem;
	}
}

/*************** Card - CSS ***************/


/*************** For Skills Card ***************/

.card-container {
	border: 2px solid #fff;
	overflow: hidden;
	box-shadow: 0rem 0rem 2rem -1rem;
	transition: 0.3s;
	-webkit-animation: ease-in;
	        animation: ease-in;
}

.card-container:hover {
	-webkit-transform: scale(1.1);
	        transform: scale(1.1);
	box-shadow: 0rem 0rem 2rem 0rem;
	cursor: pointer;
}

.card-title {
	text-align: center;
	color: #fff;
}




/*************** For Projects Card ***************/

.projects-card .projects-image-container img {
	overflow: hidden;
	background-color: #fff;
	width: 18rem;
	height: 12rem;
}

.projects-card .card-body {
	display: flex;
	justify-content: center;
	align-items: center;
	color: #fff;
}

.card-link {
	display: flex;
	justify-content: center;
	align-items: center;
	color: #fff;
}

.card-url, .card-url:hover {
	text-decoration: none;
	color: #fff;
	padding: 0.5rem;
	border: 2px solid #fff;
}

.card-url:hover {
	background-color: #fff;
	color: #000;
	-webkit-animation: enable 0.3s linear;
	        animation: enable 0.3s linear;
}

@-webkit-keyframes enable {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    60% {
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}

@keyframes enable {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    60% {
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}

/*************** Projects - CSS ***************/

.Projects {
	background: linear-gradient( rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5) ), url(/portfolio/static/media/SectionBackground.e134a9cc.jpg);
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-flow: column;
}

.projects {
	display: flex;
	flex-direction: row;
}



@media(max-width: 768px){
	.projects {
		flex-direction: column;
	}

	.projects-card .projects-image-container img {
		overflow: hidden;
		background-color: #fff;
		width: 14rem;
		height: 9rem;
	}

	.gap {
		padding-top: 1rem;
	}
}

/*************** ContactMe - CSS ***************/

.ContactMe {
	background: linear-gradient( rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5) ), url(/portfolio/static/media/SectionBackground.e134a9cc.jpg);
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	text-align: center;
}

.ContactMe h4 {
	color: #fff;
}

.contact-me-form {
	padding: 2rem;
}

.contact-me-form input, .contact-me-form textarea {
	width: 100%;
	box-sizing: border-box;
	background: none;
	border: 0;
	border: 1px solid #fff;
	border-radius: 0;
	box-shadow: none !important;
	color: #fff;
}

.contact-me-form input:focus, .contact-me-form textarea:focus {
	background: transparent !important;
	color: #fff;
	outline: none;
	border: 1px solid #f9ab00;
}

.contact-me-btn, .reset-contact-me-btn{
	width: 50%;
	padding: 1rem;
}

.error-message {
	color: #fff;
	background: red;
	text-align: left;
}

.success-message {
	font-weight: bold;
	color: green;
}
/*************** Footer - CSS ***************/

